@tailwind base;
@tailwind components;
@tailwind utilities;

/*Space between the elements*/
header ul li{
  @apply px-2;
}

/*Background color*/ 

.bg-primary{
  background:orange;
}

.bg-secondary{
  background:rgba(147, 28, 67, 0.108);
}

.mobile-nav{
  @apply fixed top-10 left-0 bg-gray-700 w-full h-1/4 ;
} 

.mobile-nav  li{
  @apply w-full h-full text-center border-b-2;
}

.project-desc{
  @apply absolute left-0 right-0 bottom-0 top-0 bg-gray-300 opacity-0 duration-500 hover:opacity-100;
}

.btn{
  @apply bg-rose-400 px-5 py-2 font-bold hover:border-2 border-black rounded;
}

html{
  scroll-behavior: smooth;
}